<template>
  <div id="list">
    <div class="content">
      <div v-for="(row,index) in rows" :key="index"
            :style="{margin:'0 '+(row.margin-3)+'px'}" 
        >
          <div v-for="(img,index) in row.row" :key="index"
              :style="{height:(360*img.height/img.width)+'px',backgroundColor:img.color}" 
          >
              <Img @mousedown.native="ShowPrev(img.id)" :src="img"/>
          </div>
      </div>
      <button v-if="images.length>0" @mousedown="Load">
        Load More
      </button>
    </div>
    
 
  </div>
</template>
<script>
import Img from './img.vue'
export default {
  components:{
    Img
  },
  props:{
    images:Array
  },
  data(){
    return {
      rows: [],
      index:0,
      top:0,
      width:1,
      height:2
    }
  },
  mounted() {
    let main = document.querySelector("#list");
    let margin=0;
    if (!main) {
      this.rows=[];
    }
    if (parseInt(main.offsetWidth / 360) < 2) {
      margin=main.offsetWidth>360?0:main.offsetWidth/2-180;
      this.rows=[{margin:margin,row:[]}];
    } else if (parseInt(main.offsetWidth / 360) >= 2 && parseInt(main.offsetWidth / 360) < 3) {
      margin=main.offsetWidth/4-180;
      this.rows=[{margin:margin,row:[]},{margin:margin,row:[]}];
    } else if (parseInt(main.offsetWidth / 360) >= 3) {
      margin=main.offsetWidth/6-180;
      this.rows=[{margin:margin,row:[]},{margin:margin,row:[]},{margin:margin,row:[]}];
    }
    
  },
  watch:{
    images:function(v,ov){
      if(v!=ov){
        v.forEach((item,index)=>{
              if(ov.length>0&&index<ov.length-1) return
              this.rows[this.index].row.push(item);
              this.index++;
              if(this.index>this.rows.length-1)
              {
                this.index=0
              }
          })
        console.log(this)  
      }
    }
  },
  methods:{
    Load(){
      this.$emit("load")
    },
    ShowPrev(id){
      this.$emit("showPrev",id)
    }
  },
  computed:{
    //size:this.a*this.b
  }

}
</script>
<style lang="scss" scoped>

#list{
  width: 100%;
  height: 100%;
  overflow: auto;
  position: relative;
  &:hover{
      &::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
        display: block;
      }
    }
  .content{
    position: relative;
    width: 100%;
    height: auto;
    overflow: auto;
    div{
        width: 360px;
        min-height: 100%;
        float: left;
        div{
          margin-bottom: 40px;
        }
    }
    button{
      position: absolute;
      bottom: 10px;
      width: 120px;
      left: calc(50% - 60px);
      height: 36px;
      background-color: rgb(49,49,49);
      color: white;
      letter-spacing: 1px;
    }
  }
  
}

</style>
