<template>
  <div id="land">
    <div class="background">
      <img :src="image.urls.custom" alt="" v-if="image!=undefined">
    </div>
    <div class="title">
      UNSPLASH
      <div class="subtitle">
        photos for everyone
      </div>
    </div>
  </div>
</template>
<script>
import { toJson } from "unsplash-js";
import { setInterval } from 'timers';
export default {
  data(){
    return {
      image:""
    }
  },
  mounted(){
    let width=document.body.clientWidth;
    let height=document.body.clientHeight;
    unsplash.photos.getRandomPhoto({ width:1920,height:1080,count:'1' })
      .then(toJson)
      .then(json => {
        let img=new Image();
        img.src=json[0].urls.custom;
        img.onload=()=>{
          this.image=json[0];
        }
      });
    setInterval(()=>{
       unsplash.photos.getRandomPhoto({ width:1920,height:1080,count:'1' })
      .then(toJson)
      .then(json => {
        let img=new Image();
        img.src=json[0].urls.custom;
        img.onload=()=>{
          this.image=json[0];
        }
      });
    },1000000);  
  }
}
</script>
<style lang="scss" scoped>
#land{
  position: absolute;
  top:0;
  right:0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  &>div{
    position: absolute;
  }
  .background{
    z-index: 1;
    top:0;
    right:0;
    width: 100vw;
    height: 100vh;
    img{
      width: 100vw;
      height: 100vh;
    }
  }
  .title{
    z-index: 2;
    top:0;
    right:0;
    width: 100vw;
    height: 100vh;
    color: white;
    font-size: 100px;
    letter-spacing: 20px;
    line-height: 100vh;
    .subtitle{
      position: absolute;
      font-style: oblique;
      color: white;
      top:6vh;
      font-size: 30px;
      letter-spacing: 2px;
      right: 33vw;
    }
  }
  
}
</style>
