<template>
  <aside>
    <div id="title">
      <img src="@/assets/img/logo.png" alt="">
      <h6>UNSPLASH</h6>
      <p>Free high-resolution photos every 10 days</p>
    </div>
    <button id="menu" @mousedown="OpenNav($event)">
       <img src="@/assets/img/menu.svg" alt="menu" srcset="">
    </button>
     <button id="search" @click="emitOpen($event)">
       <img src="@/assets/img/search.svg" alt="search" srcset="">
    </button>
  </aside>
</template>
<script>
export default {
  methods:{
    emitOpen(e){
      e.preventDefault();
      this.$emit('open',true)
    },
    OpenNav(e){
      e.preventDefault();
      this.$emit('openNav',true)
    }
  }
}
</script>
<style lang="scss" scoped>
  aside{
    position: absolute;
    width: 80%;
    height: 240px;
    background-color: #ffffff;
    top: 110px;
    left: 10%;
    z-index: 1;
    #title{
      width: 350px;
      height: 100%;
      margin: 0 auto;
      img{
        margin-top: 35px;
        margin-bottom: 10px;
        width: 64px;
        height: 64px;
      }
      h6{
        font-size: 16px;
        font-weight: 300;
        letter-spacing: 5px;
        font-weight: 600;
      }
      p{
        font-size: 14px;
        font-weight: 600;
        margin-top: 20px;
        letter-spacing: 1px;
      }
    }
    
    #menu{
      top: 20px;
      left:20px;
    }
    #search{
      top: 20px;
      right: 20px;
    }
  }
</style>
