                  "https://images.unsplash.com/profile-1524297765865-ba05b98bc6ea?ixlib=rb-1.2.1&q=80&fm=jpg&crop=faces&cs=tinysrgb&fit=crop&h=64&w=64",
                large:
                  "https://images.unsplash.com/profile-1524297765865-ba05b98bc6ea?ixlib=rb-1.2.1&q=80&fm=jpg&crop=faces&cs=tinysrgb&fit=crop&h=128&w=128"
              },
              instagram_username: "behindthesonnie",
              total_collections: 0,
              total_likes: 117,
              total_photos: 53,
              accepted_tos: true
            },
            tags: [
              { title: "people" },
              { title: "woman" },
              { title: "human" },
              { title: "person" },
              { title: "female" },
              { title: "spain" },
              { title: "sport" },
              { title: "yoga" },
              { title: "stretch" },
              { title: "fitness" },
              { title: "sports" },
              { title: "exercise" },
              { title: "working out" },
              { title: "rock" },
              { title: "manilva" },
              { title: "duquesa village" },
              { title: "model" },
              { title: "blonde" },
              { title: "portrait" },
              { title: "view" }
            ]
          }
        ]
      },
      rows: [],
      index:0
    };
  },
  mounted() {
    let main = document.querySelector("main");
    let margin=0;
    if (!main) {
      this.rows=[];
    }
    if (parseInt(main.offsetWidth / 360) < 2) {
      margin=main.offsetWidth>360?0:main.offsetWidth/2-180;
      this.rows=[{margin:margin,row:[]}];
    } else if (parseInt(main.offsetWidth / 360) >= 2 && parseInt(main.offsetWidth / 360) < 3) {
      margin=main.offsetWidth/4-180;
      this.rows=[{margin:margin,row:[]},{margin:margin,row:[]}];
    } else if (parseInt(main.offsetWidth / 360) >= 3) {
      margin=main.offsetWidth/6-180;
      this.rows=[{margin:margin,row:[]},{margin:margin,row:[]},{margin:margin,row:[]}];
    }
    
  },
  methods: {
    Search(e) {
      if (e.keyCode == 13 && this.keyWord) {
        this.rows.forEach(item=>{
              item.row=[];
            })
        unsplash.search
          .photos(this.keyWord, 1, 10)
          .then(toJson)
          .then(json => {
            
            json.results.forEach((item)=>{
              this.rows[this.index].row.push(item);
              this.index++;
              if(this.index>this.rows.length-1)
              {
                this.index=0
              }
            })

          });
      }
    },
    Clear(e){
      console.log(e)
      this.keyWord="";
    }

  }
};
