<template>
  <div class="home" @click.capture="hide($event)">
    <Header /> 
    <Aside @open="open" @openNav="OpenNav"/>
    <Search class="animated delay-1s" :class="{fadeOutDown:!isOpen,fadeInUp:isOpen}" v-if="isOpen"
     />
    <Nav v-show="nav" @closeNav="CloseNav" :me="me"/>
    <div id="imgs">
      <List :images="images" @load="Load"  @showPrev="ShowPrev" />
    </div>
    <Img @closePrev="ClosePrev()" v-if="prev" :image="prevImage" />
  </div>
</template>

<script>
import { toJson } from "unsplash-js";
// @ is an alias to /src
import Header from '../components/home/header.vue'
import Aside from '../components/home/aside.vue'
import Search from '../components/home/search.vue'
import Nav from '../components/home/nav.vue'
import List from '../components/img-list.vue'
import Img from '../components/currentImg.vue'

export default {
  name: 'home',
  components: {
    Header,Aside,Search,Nav,List,Img
  },
  data(){
    return {
      isOpen:false,
      nav:false,
      me:{},
      images:[],
      page:1,
      prev:false,
      prevImage:{}
    }
  },
  methods:{
    hide(e){
      if(e.currentTarget!=e.target) return
      this.isOpen=false;
    },
    open(v){
      this.isOpen=v;
    },
    CloseNav(){
      this.nav=false;
    },
    OpenNav(){
      this.nav=true;
    },
    Load(){
      this.page+=1
      unsplash.photos.listPhotos(this.page, 15, "popular")
      .then(toJson)
      .then(json => {
        
        this.images=this.images.concat(json);
      }); 
    },
    ClosePrev(){
      
      this.prev=false;
    },
    ShowPrev(v){
      unsplash.photos.getPhoto(v)
        .then(toJson)
        .then(json => {
          this.prevImage=json;
          this.prev=true;
          // let img=new Image();
          // img.src=json.urls.full;
          // img.onload=()=>{
            
          // }
        });
    }
  },
  mounted(){
    //popular,latest,oldest
    unsplash.currentUser.profile()
      .then(toJson)
      .then(res => {
        console.log(res)
        this.me=res;
      });
    unsplash.photos.listPhotos(1, 15, "popular")
    .then(toJson)
    .then(json => {
      
      this.images=this.images.concat(json);
    }); 
  },
  watch:{

  },
  filters:{
    format(){

    }
  }
}
let a={
  b:function(){},
  b(){

  }
}
</script>
<style lang="scss" scoped>
.home{
  position: absolute;
  top: 0;
  right: 0;
  left:0;
  bottom: 0;
  overflow: hidden;
  .animated {
    animation-delay: 0.2s;
  }
  #imgs{
    position: absolute;
    left: 10%;
    width: 80%;
    height: calc(100% - 360px);
    bottom: 0;
  }
}
</style>

