<template>
  <div id="current">
    <img :src="image.urls.regular" alt="">
    <div class="cover"></div>
    <div class="author">
      <img :src="image.user.profile_image.large" alt="" srcset="">
      <div class="description">
        <p>Photo By</p>
        <p>{{image.user.name}}</p>
      </div>
     
    </div>
    <button class="close" @mousedown="ClosePrev">
      <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="times" class="svg-inline--fa fa-times fa-w-11" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 352 512"><path fill="#ffffff" d="M242.72 256l100.07-100.07c12.28-12.28 12.28-32.19 0-44.48l-22.24-22.24c-12.28-12.28-32.19-12.28-44.48 0L176 189.28 75.93 89.21c-12.28-12.28-32.19-12.28-44.48 0L9.21 111.45c-12.28 12.28-12.28 32.19 0 44.48L109.28 256 9.21 356.07c-12.28 12.28-12.28 32.19 0 44.48l22.24 22.24c12.28 12.28 32.2 12.28 44.48 0L176 322.72l100.07 100.07c12.28 12.28 32.2 12.28 44.48 0l22.24-22.24c12.28-12.28 12.28-32.19 0-44.48L242.72 256z"></path></svg>
    </button>
    <div class="bar">
      <div class="like">
        <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="heart" class="svg-inline--fa fa-heart fa-w-16" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path :fill="image.liked_by_user?'#f80759':'#ffffff'" d="M462.3 62.6C407.5 15.9 326 24.3 275.7 76.2L256 96.5l-19.7-20.3C186.1 24.3 104.5 15.9 49.7 62.6c-62.8 53.6-66.1 149.8-9.9 207.9l193.5 199.8c12.5 12.9 32.8 12.9 45.3 0l193.5-199.8c56.3-58.1 53-154.3-9.8-207.9z"></path></svg>
        <div>{{image.likes}}</div>
      </div>
      <div class="add">
         <img src="@/assets/img/add.svg" alt="" srcset="">
      </div>
      <div class="download">
         <img src="@/assets/img/download.svg" alt="" srcset="">
      </div>
      <div class="share">
         <img src="@/assets/img/share.svg" alt="" srcset="">
      </div>
      <div class="info">
          <div>i</div>
      </div>
    </div>
    <div id="fullImg">
      <img :src="image.urls.regular"  alt="">
    </div>
  </div>
</template>
<script>
export default {
  props:{
    image:Object
  },
  data() {
    return {
      
    }
  },
  methods:{
    ClosePrev(){
      this.$emit("closePrev");
    }
  }
};
</script>
<style lang="scss" scoped>
#current {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: black;
  z-index: 100;
  &>img{
    position: absolute;
    top:0;
    left:0;
    bottom:0;
    right:0;
    width: 100%;
    height: 100%;
    filter: blur(20px);
    z-index: 1;
  }
  .cover{
    position: absolute;
    top:0;
    left:0;
    bottom:0;
    right:0;
    background-color: rgba(49,49,49,0.5);
    z-index: 2;
  }
  .author{
    position: absolute;
    left: 50px;
    top:50px;
    width: 250px;
    height: 50px;
    z-index: 3;
    text-align: left;
    img{
      width: 50px;
      height: 50px;
      border-radius: 50%;
      float: left;
    }
    .description{
      float: left;
      color: white;
      p{
        padding-left: 10px;
        height: 25px;
        line-height: 25px;
      }
    }
  }
  #fullImg{
    z-index: 3;
    position: absolute;
    top: 150px;
    height: 600px;
    left: 200px;
    right: 200px;
    &>img{
      height: 100%;
      
    }
  }
  .close{
    z-index: 3;
    position: absolute;
     top: 50px;
     right:50px;
  }
  .bar{
    z-index: 3;
    position: absolute;
    width: 750px;
    left: calc(50vw - 375px);
    height: 40px;
    bottom: 50px;
    &>div{
      float: left;
      width: 150px;
      position: relative;
      height: 40px;
      cursor: pointer;
      img,svg{
        height: 40px;
        width: 40px;
      }
      &.share>img{
        border:2px solid white;
        border-radius: 50%;
        padding: 5px;
      }
      &.info>div{
        height: 40px;
        width: 40px;
        color: white;
        line-height: 40px;
        font-size: 25px;
        border:2px solid white;
        border-radius: 50%;
        margin: 0 auto;
      }
      &.like>div{
       position: absolute;
        height: 40px;
        line-height: 40px;
        top:0;
        left: 100px;
        color: white;
      }
    }
  }
}
</style>
