<template>
  <div id="img" @mouseover="hover=true" @mouseleave="hover=false">
    <img :src="url" alt srcset :class="{hover:hover}" />
    <div class="cover" v-if="hover">
      <div class="author">
        <img :src="src.user.profile_image.large" alt="" srcset="">
        <p>Photo By</p>
        <p>{{src.user.first_name+' '+src.user.last_name}}</p>
      </div>
      <div class="bar">
         <div class="like">
           <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="heart" class="svg-inline--fa fa-heart fa-w-16" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path :fill="src.liked_by_user?'#f80759':'#ffffff'" d="M462.3 62.6C407.5 15.9 326 24.3 275.7 76.2L256 96.5l-19.7-20.3C186.1 24.3 104.5 15.9 49.7 62.6c-62.8 53.6-66.1 149.8-9.9 207.9l193.5 199.8c12.5 12.9 32.8 12.9 45.3 0l193.5-199.8c56.3-58.1 53-154.3-9.8-207.9z"></path></svg>
          </div> 
         <div class="add">
           <img src="@/assets/img/add.svg" alt="" srcset="">
         </div>
         <div class="download">
           <img src="@/assets/img/download.svg" alt="" srcset="">
         </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props:{
    src:Object
  },
  data(){
    return {
      url:"",
      hover:false
    }
  },
  mounted(){
    console.log(this.src)
    let img=new Image();
    img.src=this.src.urls.regular;
    img.onload=()=>{
      
       this.url=img.src;
      // this.$el.attributes[2].value=img.src;
    }
  }
};
</script>
<style lang="scss" scoped>
#img{
  position: relative;
  width: 100%;
  height: 100%;
  cursor: pointer;
  &>img {
    position: absolute;
    top: 0;
    left:0;
    width: 100%;
    height: 100%;
    box-shadow: 0 10px 15px -5px rgb(149, 149, 149);
    z-index: 1;
    &.hover{
      filter: blur(2px)
    }
  }
  .cover{
    top: 0;
    left:0;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 2;
    background-color: rgba(0,0,0,0.2);
    cursor: zoom-in;
    .author{
      position: absolute;
      top:calc(50% - 80px);
      width: 100%;
      height: 100px;
      color: white;
      padding-top: 70px;
      cursor: pointer;
      img{
        position: absolute;
        top:0;
        width: 64px;
        height: 64px;
        border-radius: 50%;
        left: calc(50% - 32px);
      }
      
    }
    .bar{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      height: 80px;
      color: white;
      div{
        top:20px;
        width: 40px;
        height: 40px;
        cursor: pointer;
        svg{
          color: #ffffff;
          width: 40px;
          height: 40px;
        }
        img{
          
          width: 40px;
          height: 40px;
        }
      }  
      .like{
        position: absolute;
        left: 20px;
      }
      .add{
        position: absolute;
        left: calc(50% - 20px);
      }
      .download{
        position: absolute;
        right: 20px;
      }
   }
  }
}

</style>
